import React, {FC} from 'react';

import './Card.scss';

interface ICardProps {
    name: string;
    imageHref: string;
    imageAlt: string;
    path: string;
}

const setPath = (path: string) => {
    window.location.hash = path;
}

const Card: FC<ICardProps> = (props) => {
    //const maxHeight = props.maxHeight ?? "30em";
    return (
        <>
            <div className="card" onClick={() => setPath(props.path)}>
                <div className="title-container">
                    <div className="title">
                        <h1>{props.name}</h1>
                    </div>
                    <div className="title">
                        <h1>{props.name}</h1>
                    </div>
                </div>
                <div className="content">{props.children}</div>
                <div className="images">
                    <img src={props.imageHref} alt={props.imageAlt} />
                </div>
            </div>
        </>
    )
}

export default Card;