import React, { FC, useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';

import "./Article.scss";


const Article: FC<{markdownPath: string, imagePath: string}> = ({markdownPath, imagePath}) => {
    const [md, setMd] = useState("");

    useEffect(() => {
        const asyncEffect = async () => {
            
            const markdown = await fetch(markdownPath);
            setMd(await markdown.text());
        }

        asyncEffect();
    }, [setMd, markdownPath])


    const bgStyle={
        background: 
            `linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0) 30%, rgba(255,255,255,1) 60%),url('${imagePath}')`,
        backgroundSize: "cover",
    }

    return (
        <>
        <div className="background-image" style={bgStyle}/>
        <div className="content-container">
            <div className="content">
                <div style={{width: "100%", maxWidth: "800px"}}>
                    <ReactMarkdown source={md} />
                </div>
            </div>
        </div>
        </>
    )
}

export default Article;