import React from 'react';
import ReactDOM from 'react-dom';
import Home from './Pages/Home';
import {HashRouter, Route, Switch} from "react-router-dom";
import * as serviceWorker from './serviceWorker';
import Header from './Components/Header';

import './Root.scss';
import Article from './Pages/Article';


ReactDOM.render(
  <React.StrictMode>
    <Header />   
      <HashRouter>
        <Switch>
          <Route path="/siteLab">
            <Article markdownPath="/markdown/siteLab.md" imagePath="/images/sitelab_article_3.png" />
          </Route>
          <Route path="/particulate">
            <Article markdownPath="/markdown/particulate.md" imagePath="/images/particulate_1.png" />
          </Route>
          <Route path="/bacskywalk">
            <Article markdownPath="/markdown/bacskywalk.md" imagePath="/images/skywalk_1.png" />
          </Route>
          <Route path="/" component={Home} />
        </Switch>
      </HashRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
