
import React, { FC } from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAt } from "@fortawesome/free-solid-svg-icons"
import { faTwitter, faGithub } from "@fortawesome/free-brands-svg-icons"
import './Header.scss';


const Header: FC = () => {
    
    return (
      <div className="header-container">          
        <div className="header">    
            <h1 style={{gridArea: "name"}}><a href="#/" >Chris Dirkis</a></h1>
            <p style={{gridArea: "email"}}>
              <FontAwesomeIcon size="1x" icon={faAt} />
              <a href="mailto:me@chrisdirk.is">me@chrisdirk.is</a>
            </p>
            <p style={{gridArea: "twitter"}}>
              <FontAwesomeIcon size="1x" icon={faTwitter} />
              <a href="https://twitter.com/chrisdirkis">@chrisdirkis</a>
            </p>
            <p style={{gridArea: "github"}}>
              <FontAwesomeIcon size="1x" icon={faGithub} />
              <a href="https://github.com/ChrisDirkis">ChrisDirkis</a>
            </p>
        </div>
        <div className="stripes">
            <div className="s3" />
            <div className="s2" />
            <div className="s1" />
        </div>
      </div>
    );
    

}

export default Header;